.search-bar-wrapper {
	position: fixed;
	width: calc(100% - 2em);
	padding: 1em 0;
	z-index: 1000;
}

.plant-details .img-wrapper {
	text-align: center;
}

.plant-details .img-wrapper p {
	text-align: left;
	margin-bottom: 1em;
}

.loader-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 100%;
}

.plant-results.no-results {
	text-align: center;
	font-style: italic;
}

.plant-results-loader {
	padding: 4em !important;
}

.plant-results.plant-name {
	text-transform: capitalize;
}

.header-component {
	width: 100%;
	text-align: center;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	position: fixed;
	z-index: 2000;
}

* {
	box-sizing: border-box;
}

img {
	max-width: 100% !important;
	margin-bottom: 1em;
}

.lrg-header {
	font-size: 5em !important;
}

.med-font {
	font-size: 1.4em !important;
	padding: 10px 0;
}

.sm-font {
	font-size: 1.2em !important;
	padding: 0 0 5px;
}

/* FONT COLORS  */
.font-main-green {
	color: #698f3f !important;
}

.font-main-blue {
	color: #68c5db !important;
}

.font-main-white {
	color: #fbfcfd !important;
}

.font-off-white {
	color: #edf2f4 !important;
}

.font-disabled {
	color: gray !important;
}

/* BACKGROUND COLORS */
.bg-main-green {
	background-color: #698f3f;
}

.bg-main-blue {
	background-color: #68c5db;
}

.bg-main-white {
	background-color: #fbfcfd;
}

.bg-off-white {
	background-color: #f3f3f3;
}

.main-content {
	padding-top: 99px;
}

.plant-results-wrapper {
	margin-top: 66px;
}

